@font-face {
    font-family: 'Abhaya';
         src: url('./fonts/AbhayaLibre-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'CodecColdExtraBold';
    src: url('fonts/CodecColdExtraBold.eot');
    src: url('fonts/CodecColdExtraBold.eot') format('embedded-opentype'),
         url('fonts/CodecColdExtraBold.woff2') format('woff2'),
         url('fonts/CodecColdExtraBold.woff') format('woff'),
         url('fonts/CodecColdExtraBold.ttf') format('truetype'),
         url('fonts/CodecColdExtraBold.svg#CodecColdExtraBold') format('svg');
}

@font-face {
	 font-family: "LLCircularWeb-Black";
	 src: 	 url("fonts/lineto-circular-black-c.eot");
	 src: 	 url("fonts/lineto-circular-black-c.eot?#iefix") format("embedded-opentype"),
		 url("fonts/lineto-circular-black-c.woff") format("woff"),
		 url("fonts/lineto-circular-black-c.ttf") format("truetype"),
		 url("fonts/lineto-circular-black-c.svg#LLCircularWeb-Black") format("svg");
	 font-weight: normal;
	 font-style: normal;
}

@font-face {
	 font-family: "LLCircularWeb-Book";
	 src: 	 url("fonts/lineto-circular-book-c.eot");
	 src: 	 url("fonts/lineto-circular-book-c.eot?#iefix") format("embedded-opentype"),
		 url("fonts/lineto-circular-book-c.woff") format("woff"),
		 url("fonts/lineto-circular-book-c.ttf") format("truetype"),
		 url("fonts/lineto-circular-book-c.svg#LLCircularWeb-Book") format("svg");
